<template>
  <v-bottom-sheet 
    v-model="openQuestion"
    :persistent="true"
    :hide-overlay="true"
    color="primary"
  >
    <v-container fluid pa-0>
      <v-row no-gutters>
        <v-col cols="12">
          <v-list>
            <!-- image -->
            <v-col cols="12" v-if="question.image">
              <img
                v-bind:src="question.image"
                v-bind:alt="'Could not display ' + image"
                style="height: 320px; width: 100%"
              />
            </v-col>
            <!-- question -->
            <v-col cols="12" style="padding-bottom: 0%;">
              <!-- <v-tooltip right>
              <template v-slot:activator="{ on, attrs }">
                <span
                  @click="closeQuestion"
                  color="primary"
                  v-bind="attrs"
                  v-on="on"
                  text
                ><v-icon>clear</v-icon></span>
              </template>
              <span>Close Question</span>
              </v-tooltip> -->
              <h2>[ <span class="font-weight-regular red--text">{{ question.container }}</span> ]: {{ question.question }}</h2>
            </v-col>
            <!-- input -->
            <v-col cols="12" v-if="question.options.length === 0">
              <v-text-field
                style="font-size:22px;"
                label="Type Your Input Here."
                autosize
                autofocus
                outlined
                clearable
                :type="textType"
                v-model="userInput"
                v-on:keyup.enter="answerQuestion"
              >
                <v-btn slot="append-outer" large color="primary" style="top: -16px" @click="answerQuestion">
                  <v-icon left>send</v-icon>
                  Submit
                </v-btn>
              </v-text-field>
            </v-col>
            <!-- select -->
            <v-col cols="12" v-else>
              <v-select
                :items="question.options"
                label="Select Your Option Here."
                outlined
                clearable
                :multiple="question.multiple"
                v-model="userInput"
                :error-messages="selectError"
              >
              
                <template v-slot:prepend-item v-if="question.multiple">
                  <v-list-item
                  ripple
                    @click="toggleSelect"
                  >
                    <v-list-item-action>
                      <v-icon :color="userInput.length > 0 ? 'indigo darken-4' : ''">{{ selectIcon }}</v-icon>
                    </v-list-item-action>
                    <v-list-item-content>
                      <v-list-item-title>Select All</v-list-item-title>
                    </v-list-item-content>
                  </v-list-item>
                  <v-divider class="mt-2"></v-divider>
                </template>

                <v-btn slot="append-outer" large color="primary" style="top: -16px" @click="answerQuestion">
                  <v-icon left>send</v-icon>
                  Submit
                </v-btn>
              </v-select>
            </v-col>
          </v-list>
        </v-col>
      </v-row>
    </v-container>
  </v-bottom-sheet>
</template>

<script>
import { KunlunApi } from '../api'

export default {
  name: 'AskQuestion',
  props: ['question', 'containerName'],

  data: () => ({
    userInput: Object,
    selectError: '',
    open: true
  }),
  computed: {
    openQuestion () {
      return this.question.container === this.containerName && this.open
    },
    textType () {
      if (this.question.visible) {
        return 'text'
      } else {
        return 'password'
      }
    },
    likesAllOptions () {
      return this.userInput.length === this.question.options.length
    },
    likesSomeOptions () {
      return this.userInput.length > 0 && !this.likesAllOptions
    },
    selectIcon () {
      if (this.likesAllOptions) return 'check_box'
      if (this.likesSomeOptions) return 'indeterminate_check_box'
      return 'check_box_outline_blank'
    }
  },
  watch: {
    question () {
      this.open = true
    }
  },
  mounted () {
    if (this.question.options.length) {
      this.userInput = []
    } else {
      this.userInput = ''
    }
  },
  methods: {
    answerQuestion () {
      if (this.question.options.length && this.userInput.length < 1) {
        this.selectError = 'Must Select one at least !'
        return false
      }
      this.open = false
      KunlunApi({ service: 'answer-question', answer: this.userInput, container: this.containerName })
        .then(response => {
          const data = response.data
          console.log(data)
          this.selectError = ''
          this.userInput = ''
        })
        .catch(e => {
          console.log(e)
        })
    },
    closeQuestion () {
      this.userInput = ''
    },
    toggleSelect () {
      this.$nextTick(() => {
        if (this.likesAllOptions) {
          this.userInput = []
        } else {
          this.userInput = this.question.options.slice()
        }
      })
    }
  }
}
</script>
<style scoped>

</style>