<template>
  <v-container fluid pa-0>
    <v-row no-gutters>
      <v-col cols="12" style="margin-top: -22px;">
        <v-hover v-slot="{ hover }">
          <v-card
            :elevation="hover ? 5 : 5"
            :class="{ 'on-hover': hover }"
            :color="toolBarColor"
            height="50px"
          >
            <v-card-title>
            <h3 :class="textColor + ' font-weight-light'" style="margin-top: -10px;">
              平台版本: {{ version }}
            </h3>
            <v-spacer></v-spacer>
            <v-menu offset-y>
            <template v-slot:activator="{ on, attrs }">
              <v-btn
                color="error"
                class="white--text"
                style="margin-top: -12px;"
                large
                v-bind="attrs"
                v-on="on"
              >
                {{ mode }}
              </v-btn>
            </template>
            <v-list>
              <v-list-item
                v-for="(item, index) in items"
                :key="index"
                @click="setMode(item)"
              >
                <v-list-item-title>{{ item }}</v-list-item-title>
              </v-list-item>
            </v-list>
          </v-menu>
          </v-card-title>
          </v-card>
        </v-hover>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>

export default {
  name: 'ToolBar',
  props: ['version'],
  components: {
  },
  data: () => ({
    toolBarColor: 'purple',  // yellow
    textColor: 'white--text',
    items: ['PRODUCT', 'DEVELOP'],
    mode: 'PRODUCT',
  }),
  mounted () {
  },
  beforeDestroy () {
  },
  methods: {
    setMode (mode) {
      this.mode = mode
      if (this.mode === 'PRODUCT') { this.toolBarColor = 'purple', this.textColor = 'white--text' }
      if (this.mode === 'DEVELOP') { this.toolBarColor = 'yellow', this.textColor = 'black--text' }
      this.$emit("setMode", mode)
    }
  }
}
</script>
